import image28 from '@/assets/image/gallery2/26_27 마트 스프레드-2.jpg'
import image29 from '@/assets/image/gallery2/26_27 마트 스프레드-3.jpg'
import image30 from '@/assets/image/gallery2/28_THE_2759-.jpg'
import image31 from '@/assets/image/gallery2/29_THE_2771-.jpg'


import {ref} from "vue";
import {useSwipe} from '@vueuse/core';
import {NaverMap} from 'vue3-naver-maps'
import {NaverMarker} from 'vue3-naver-maps'


export default {
  name: 'HelloWorld',
  props: {
    msg: String
  },
  components: {
    NaverMap,
    NaverMarker,
  },
  setup() {
    const refModal = ref(null);
    const player = ref(null);
    const scrollTopRefModal = () => {
      refModal.value.scrollTop = 0;
    };

    const swipeArea = ref(null);
    const direction = ref('');

    const {isSwiping, direction: swipeDirection} = useSwipe(swipeArea, {
      threshold: 50, // 스와이프 인식 최소 거리 (픽셀 단위)
    });

    return {
      refModal,
      scrollTopRefModal,
      player,
      swipeArea,
      direction,
      swipeDirection,
      isSwiping
    }
  },
  data() {
    return {
      backAccount: [
        '3333097666325',
        '10009052259265',
        '3333126496902',
        '110-522-594186',
        '110-518-471282',
      ],
      showContractGroom: false,
      showContractBride: false,
      showAudioAlert: true,
      remainingDay: 0,
      remainingHour: 0,
      remainingMin: 0,
      remainingSec: 0,
      images: [
        image01,
        image03,
        image04,
        image05,
        image07,
        image08,
        image09,
        image10,
        image11,
        image12,
        image13,
        image14,
        image15,
        image16,
        image17,
        image18,
        image19,
        image21,
        image22,
        image23,
        image24,
        image26,
        image27,
        image28,
        image29,
        image30,
        image31
      ],
      showImageModal: false,
      showImage: null,
      imageIndex: null,
      width: 800,
      height: 800,
      info: false,
      marker: null,
      count: 1,
      map: null,
      isCTT: false,
      initLayers: ['BACKGROUND', 'BACKGROUND_DETAIL', 'POI_KOREAN', 'TRANSIT', 'ENGLISH', 'CHINESE', 'JAPANESE'],
      mapOptions: {
        latitude: 37.5616037, // 지도 중앙 위도
        longitude: 126.8532854, // 지도 중앙 경도
        zoom: 14,
      },
      currentId: 1,
      list: [
        {id: 1, label: '화환', content: '화환은 정중히 사양합니다.'},
        {id: 2, label: '식사안내', content: '7층 연회장에서 뷔페식사입니다.'},
        {id: 3, label: '주차안내', content: '2시간 무료주차 가능합니다. (주차대수 800대)'}
      ],
      showContract: false,
    }
  },
  mounted() {
    setInterval(() => {
      this.timeRemaining();
    }, 1000);
  },
  watch: {
    currentId: function (val) {
      console.log(val)
    },
    // direction이 변할 때마다 이를 반응형으로 처리
    swipeDirection: function (newDirection) {
      if (this.isSwiping) {
        this.direction = newDirection;
        if (newDirection === 'left') {
          this.imageIndexMinus()
        } else if (newDirection === 'right') {
          this.imageIndexPlus()
        }
      }
    },
  },
  computed: {
    current() {
      return this.list.find(el => el.id === this.currentId) || {}
    }
  },
  methods: {
    clickCopyBankAccount(index) {
      const data = this.backAccount[index]

      let tempInput = document.createElement("input"); // 1
      tempInput.value = data; // 3


      document.body.appendChild(tempInput); // 4
      tempInput.select();
      document.execCommand("copy"); // 5
      document.body.removeChild(tempInput); // 6
      alert("계좌번호가 복사되었습니다.")
    },
    clickAudio() {
      this.showAudioAlert = !this.showAudioAlert;
      if (this.showAudioAlert) {
        this.player.play()
      } else {
        this.player.pause()
      }
    },
    timeRemaining() {
      let today = new Date();
      let gap = new Date("2024-09-29 00:00:00").getTime() - today;

      this.remainingDay = String(Math.floor(gap / (1000 * 60 * 60 * 24)));
      this.remainingHour = String(Math.floor((gap / (1000 * 60 * 60)) % 24));
      this.remainingMin = String(Math.floor((gap / (1000 * 60)) % 60));
      this.remainingSec = String(Math.floor((gap / 1000) % 60));

      this.remainingDay = this.remainingDay >= 10 ? this.remainingDay : "0" + this.remainingDay;
      this.remainingHour = this.remainingHour >= 10 ? this.remainingHour : "0" + this.remainingHour;
      this.remainingMin = this.remainingMin >= 10 ? this.remainingMin : "0" + this.remainingMin;
      this.remainingSec = this.remainingSec >= 10 ? this.remainingSec : "0" + this.remainingSec;
    },
    imageClick(image, index) {
      this.showImageModal = true;
      this.showImage = image;
      this.imageIndex = index
    },
    imageIndexMinus() {
      let tempIndex = this.imageIndex;
      let minusIndex = tempIndex - 1;
      if (minusIndex < 0) {
        this.showImage = this.images[this.images.length - 1];
        this.imageIndex = this.images.length - 1;
      } else {
        this.showImage = this.images[minusIndex];
        this.imageIndex = minusIndex;
      }
      console.log()
    },
    imageIndexPlus() {
      let tempIndex = this.imageIndex;
      let imageLength = this.images.length;
      let plusIndex = tempIndex + 1;
      if (plusIndex >= imageLength) {
        this.showImage = this.images[0];
        this.imageIndex = 0;
      } else {
        this.showImage = this.images[plusIndex];
        this.imageIndex = plusIndex;
      }
    },
    newTabOpen(url) {
      window.open(url)
    },
    contractClick() {
      this.showContract = true;
    },
    copyLink() {
      const data = "https://ohus1989.github.io/"; // 2

      let tempInput = document.createElement("input"); // 1
      tempInput.value = data; // 3


      document.body.appendChild(tempInput); // 4
      tempInput.select();
      document.execCommand("copy"); // 5
      document.body.removeChild(tempInput); // 6
      alert("주소가 복사되었습니다.")
    },
    sendKakao: function () {
      window.Kakao.init('7ad882bd08641f3c11edc61ed13ced92');
      window.Kakao.Link.sendDefault({
        objectType: 'feed',
        content: {
          title: '오의섭 ♥ 홍주현 결혼합니다.',
          description: '9/29 결혼식에 초대합니다.',
          imageUrl:
              'https://ohus1989.github.io/img/01_THE_0955_2-.06c1f277.jpg',
          link: {
            mobileWebUrl: 'https://ohus1989.github.io/',
            webUrl: 'https://ohus1989.github.io/',
          },
        },
        buttons: [
          {
            title: '오의섭 ♥ 홍주현 결혼합니다.',
            link: {
              mobileWebUrl: 'https://ohus1989.github.io/',
              webUrl: 'https://ohus1989.github.io/',
            },
          },
        ],
      })
    },
  },
}
</script>

